import React, { Component } from 'react';
import INPUTS from "../common/InputFields";
class MemberShipForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            creditCardNumber: "",
            Fullname: "",
            expireDate: "",
            CSV: "",
            redeem: "",
            fullName: "",
            address: "",
            city: "",
            postCode: "",
            country: "",
            errors: {
                creditCardNumber: "",
                Fullname: "",
                expireDate: "",
                CSV: "",
                redeem: "",
                fullName: "",
                address: "",
                city: "",
                postCode: "",
                country: "",
            }
        };
    }
    setStateHandler = val => {
        this.setState({
            errors: val.errors,
            [val.name]: val[val.name]
        })
    }
    render() {
        return (
            <React.Fragment>
                <div className="container-fluid MemberPayment">
                    <div className="row BG">
                        <h3>Get Premium Membership - $9.9 / Mo</h3>
                        <p>Cancel anytime, no questions asked.</p>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-6 col-12">
                                    <h6>Credit Card Info</h6>
                                    <div className="Content">
                                        <INPUTS type="number" name="creditCardNumber" label="CARD NUMBER" place="1234 1234 1234 1234" width="100%" divW="70%" state={this.state} stateHandler={this.setStateHandler} />
                                        <div className="CardType">
                                            <img src="./img/visa.png" alt="" />
                                        </div>
                                    </div>
                                    {this.state.errors.creditCardNumber !== "" ? <span className="error text-danger">{this.state.errors.creditCardNumber}</span> : ""}
                                    <div className="Content">
                                        <INPUTS type="text" name="Fullname" label="CARDHOLDER NAME" place="Cardholder Name" width="100%" divW="100%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                    {this.state.errors.Fullname !== "" ? <span className="error text-danger">{this.state.errors.Fullname}</span> : ""}
                                    <div className="Content">
                                        <INPUTS type="text" name="expireDate" label="EXPIRE DATE" place="01/01" icon="fas fa-chevron-down" width="100%" divW="40%" state={this.state} stateHandler={this.setStateHandler} />
                                        <INPUTS type="text" name="CSV" label="CSV" place="***" width="100%" icon="fas fa-chevron-down" divW="40%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                    <div className="Content">
                                        <INPUTS type="text" name="redeem" label="REDEEM PROMO CODE" place="Enter Code" width="100%" divW="70%" state={this.state} stateHandler={this.setStateHandler} />
                                        <button className="btn btn-light ml-md-0 ml-3">Redeem</button>
                                    </div>
                                    {this.state.errors.redeem !== "" ? <span className="error text-danger">{this.state.errors.redeem}</span> : ""}
                                </div>
                                <div className="col-md-6 col-12">
                                    <h6>Billing Info</h6>
                                    <div className="Content">
                                        <INPUTS type="text" name="fullName" label="FULL NAME" place="e.g John" width="100%" divW="100%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                    {this.state.errors.fullName !== "" ? <span className="error text-danger">{this.state.errors.fullName}</span> : ""}
                                    <div className="Content">
                                        <INPUTS type="text" name="address" label="ADDRESS" place="House #7, Steet z" width="100%" divW="100%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                    {this.state.errors.address !== "" ? <span className="error text-danger">{this.state.errors.address}</span> : ""}
                                    <div className="Content">
                                        <INPUTS type="text" name="city" label="CITY" place="Califonia" width="100%" icon="fas -fa-chevron-down" divW="40%" state={this.state} stateHandler={this.setStateHandler} />
                                        <INPUTS type="text" name="postCode" label="POST CODE" place="00000" icon="fas fa-chevron-down" width="100%" divW="40%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                    <div className="Content">
                                        <INPUTS type="text" name="country" label="COUNTRY" place="--USA" icon="fas fa-chevron-down" width="100%" divW="100%" state={this.state} stateHandler={this.setStateHandler} />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <footer className="col-12">
                                <button className="btn Trial">Start Free Trial</button>
                                <span>You will be billed after 14 days</span>
                                <button className="btn">Cancel</button>
                            </footer>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default MemberShipForm;